import { fromJS } from 'immutable'

/* @flow */
// ------------------------------------
// Constants
// ------------------------------------
export const CREATE_PROJECT = 'CREATE_PROJECT'
export const UPDATE_PROJECT = 'UPDATE_PROJECT'
export const DELETE_PROJECT = 'DELETE_PROJECT'

// ------------------------------------
// Actions
// ------------------------------------
// NOTE: "Action" is a Flow interface defined in https://github.com/TechnologyAdvice/flow-interfaces
// If you're unfamiliar with Flow, you are completely welcome to avoid annotating your code, but
// if you'd like to learn more you can check out: flowtype.org.
// DOUBLE NOTE: there is currently a bug with babel-eslint where a `space-infix-ops` error is
// incorrectly thrown when using arrow functions, hence the oddity.
export function create (project = { title: '', description: '', deadline: '' }) {
  return {
    type: CREATE_PROJECT,
    project: project
  }
}

export function update (index = 0, project = { index: '', description: '', deadline: '' }) {
  return {
    type: UPDATE_PROJECT,
    index: index,
    project: project
  }
}

export function remove (index = 0) {
  return {
    type: DELETE_PROJECT,
    index: index
  }
}

export const actions = {
  create,
  update,
  remove
}

// ------------------------------------
// Action Handlers
// ------------------------------------
const ACTION_HANDLERS = {
  [CREATE_PROJECT]: (state, action) => state.push(fromJS(action.project)),
  [UPDATE_PROJECT]: (state, action) => state.updateIn([action.index], (project) => fromJS(action.project)),
  [DELETE_PROJECT]: (state, action) => state.deleteIn([action.index])
}

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = fromJS([])
export default function projectReducer (state = initialState, action) {
  const handler = ACTION_HANDLERS[action.type]
  return handler ? handler(state, action) : state
}



