import React, { PropTypes } from 'react'
import { connect } from 'react-redux'
import { create } from '../../redux/modules/project'
import { selectProject } from '../../redux/modules/active'
import AddProject from '../../components/AddProject/AddProject'
import ProjectItem from '../../components/ProjectItem/ProjectItem'
import classes from './SidenavView.scss'

export class Sidenav extends React.Component {
  static propTypes = {
    create: PropTypes.func.isRequired,
    select: PropTypes.func.isRequired,
    projects: PropTypes.any.isRequired
  }

  render () {
    let _this = this
    let projectList = this.props.projects.map(function (project, k) {
      return <ProjectItem key={k} project={project} select={_this.props.select}/>
    })

    return (
      <div className={classes['sidenav-container']}>
        <div className={classes['sidenav-header']}>
          <img src='http://placehold.it/200x100' className={classes.logo} />
        </div>
        <div className={classes['sidenav-lists']}>
          <AddProject create={this.props.create} />
          {projectList}
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    projects: state.project
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    create: (project) => dispatch(create(project)),
    select: (project) => dispatch(selectProject(project))
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Sidenav)



