import React, { PropTypes } from 'react'
import { findDOMNode } from 'react-dom'
import classes from '../ProjectItem/ProjectItem.scss'

export class ProjectMenuDropdown extends React.Component {
  static propTypes = {
    project: PropTypes.object.isRequired
  }

  constructor (props) {
    super(props)
    this.state = {
      isOpen: false
    }
    this.renderDropdown = this.renderDropdown.bind(this)
    this.handleClick = this.handleClick.bind(this)
    this.closeDropdown = this.closeDropdown.bind(this)
  }

  componentDidUpdate () {
    if(this.state.isOpen) {
      findDOMNode(this.refs.menu).style.opacity = 1
      findDOMNode(this.refs.menu).style.visibility = 'visible'
    }
  }

  render () {
    return (
      <a ref='menu' href='#' className={classes['menu-toggle']} onClick={this.handleClick}>
        <i className='glyphicon glyphicon-option-vertical'></i>
        {this.renderDropdown()}
        {this.renderBackground()}
      </a>
    )
  }

  handleKeyDown (e) {
    if (e.CharCode === 27) this.closeDropdown
  }

  handleClick () {
    this.setState({ isOpen: !this.state.isOpen })
  }

  closeDropdown() {
    findDOMNode(this.refs.menu).style.opacity = ''
    findDOMNode(this.refs.menu).style.visibility = ''
    this.setState({ isOpen: false })
  }

  renderDropdown () {
    if (this.state.isOpen) {
      return (
        <div className={classes['dropdown-menu']}>
          <div className={classes.item}>Edit Project</div>
          <div className={classes.item}>Delete Project</div>
        </div>
      )
    }
  }

  renderBackground () {
    if (!this.state.isOpen) return null
    return <div style={{position: 'fixed', top: 0, left: 0, right: 0, bottom: 0, zIndex: 999}} onClick={this.closeDropdown} />
  }
}

export default ProjectMenuDropdown




