import React, { PropTypes } from 'react'
import { Link } from 'react-router'
import classes from './ProjectItem.scss'
import ProjectMenuDropdown from '../ProjectMenuDropdown/ProjectMenuDropdown'

export class ProjectItem extends React.Component {
  static propTypes = {
    project: PropTypes.object.isRequired
  }

  render () {
    return (
      <div className={classes['project-item']} >
        <Link to={{ pathname: '/' + this.props.project.get('title') }}
          className={classes['link']} activeClassName={classes['active']}>
          {this.props.project.get('title')}
        </Link>
        <ProjectMenuDropdown project={this.props.project}/>
      </div>
    )
  }
}

export default ProjectItem




