import React, { PropTypes } from 'react'
import classes from './AddProject.scss'

export class AddProject extends React.Component {
  static propTypes = {
    create: PropTypes.func.isRequired
  }

  constructor (props) {
    super(props)
    this.state = { title: '', isCreating: false }
    this.handleChange = this.handleChange.bind(this)
    this.handleKeyPress = this.handleKeyPress.bind(this)
    this.addProject = this.addProject.bind(this)
  }

  render () {
    return (
      <div className={classes['add-project']}>
        <label className={classes.title}>PROJECTS</label>
        <input type='text'
          placeholder='PROJECT NAME'
          onKeyPress={this.handleKeyPress}
          onChange={this.handleChange}
          value={this.state.title}
          className={this.state.isCreating ? classes.input + ' ' + classes.show : classes.input}
          autoFocus
           />
        <button className={classes.button} onClick={this.addProject}>
          <i className={this.state.isCreating ? 'glyphicon glyphicon-remove' : 'glyphicon glyphicon-plus'}></i>
        </button>
      </div>
    )
  }

  handleChange (e) {
    this.setState({ title: e.target.value })
  }

  handleKeyPress (e) {
    if (e.charCode === 13) {
      this.addProject()
    }
  }

  addProject () {
    if (this.state.isCreating) {
      let project = { title: this.state.title }
      if (this.state.title) this.props.create(project)
      this.setState({ title: '', isCreating: false })
      return
    }

    this.setState({ isCreating: true })
  }
}

export default AddProject




