import React, { PropTypes } from 'react'
import { connect } from 'react-redux'
import { selectProject } from '../redux/modules/active'

export class ProjectDetail extends React.Component {
  static propTypes = {
    project: PropTypes.object.isRequired,
    select: PropTypes.func.isRequired,
    params: PropTypes.object.isRequired
  }

  componentWillReceiveProps (nextProps) {
    if (this.props.params.projectID !== nextProps.params.projectID) {
      this.props.select({ title: nextProps.params.projectID })
    }
  }

  render () {
    return (
      <div className='col-md-9'>
        <h2>{this.props.project.get('title')}</h2>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    project: state.active.get('project')
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    select: (projectID) => dispatch(selectProject(projectID))
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProjectDetail)



